.journalLayout {
  position: relative;
  grid-column: 1/2;
  margin-top: 100px;
  h2 {
    position: absolute;
    color: white;
    background: #248a83;
    width: 300px;
    padding-right: 70px;
    padding-left: 10px;
    padding-top: 10px;
    padding-bottom: 10px;
    margin-top: 30px;
    font-family: $title_font_sub;
    font-size: 25px;
    text-transform: uppercase;
  }
  img {
    filter: brightness(100%);
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  p {
    font-family: $description_font;
  }
  #text {
    font-family: $description_font;
    font-size: 16px;
  }
}

.flex {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.dateAuthor {
  display: flex;
  background: #248a83;
  width: 200px;
  bottom: 30px;
  right: 0px;
  p {
    color: white;
    padding-left: 10px;
    padding-bottom: 10px;
    padding-top: 10px;
  }
}

.posts {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  margin-left: 100px;
  h2 {
    margin-top: 70px;
    font-family: $title_font;
  }
  h3 {
    font-family: $title_font;
    grid-column: 1;
    padding-bottom: 20px;
    margin-top: 20px;
  }
  p {
    grid-column: 1;
  }
}

.sidebar {
  grid-column: 2/3;
  grid-row: 1/3;
  padding-left: 100px;
  margin-top: 70px;
}

.widget-hours1 {
  font-family: $title_font;
  color: #2f1339;
  font-size: 20px;
  margin-bottom: 20px;
}

#text-3 p {
  color: #248a83;
  font-family: $description_font;
}

#text-4 p {
  color: black;
  font-family: $description_font;
}

#archives-3 a {
  color: #248a83;
  text-decoration: none;
  font-family: $description_font;
}
